.MoviesContainer {
  align-items: flex-end;
  color: #ffe81f;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100vh;
}

.MoviesContainer-planets {
  align-content: space-around;
  justify-content: space-evenly;
  display: flex;
  width: 60%;
  height: 80vh;
  flex-wrap: wrap;
  overflow: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none
}

.MoviesContainer-planets::-webkit-scrollbar { 
    display: none;
}

.MoviesContainer-loading {
  height: 25%;
  width: 25%;
}

.MoviesContainer__h1--Episodes {
  margin-right: 25%;
}
